import React, { useEffect, useState } from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';
import Banner from './Banner';
import TitleContent from './TitleContent';
import sunflower from '../assets/images/sunflower.webp';
import { Accordion, Card, useAccordionButton } from 'react-bootstrap';
import { ChevronDown, ChevronUp } from 'react-bootstrap-icons';

function CustomToggle({ children, eventKey }) {
  const [open, setOpen] = useState(false);
  const decoratedOnClick = useAccordionButton(eventKey, () => {
    setOpen(!open);
  });

  return (
    <Card.Header onClick={decoratedOnClick} style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', cursor: 'pointer' }}>
      {children}
      {open ? <ChevronUp /> : <ChevronDown />}
    </Card.Header>
  );
}

function HomeContent() {
  useEffect(() => {
    AOS.init({ duration: 1000 });
  }, []);

  return (
    <div className="home-content">
      <Banner src={sunflower} />
      <TitleContent 
        title="Bienvenue" 
        subtitle="Vous vous demandez peut-tre ce qu'est la sophrologie, en quoi consiste t-elle, et qu'apporte t-elle dans votre quotidien ?" 
      />
      <div className="home-content__three-columns">
        <div className="home-content__column" data-aos="flip-right" data-aos-offset="200">
          <h2>Petite histoire...</h2>
          <div className="home-content__column-content">
            <p>La sophrologie est une mthode de relaxation et de dveloppement personnel fonde en 1960 par le neuropsychiatre Alfonso Caycedo. Originaire de Colombie, A. Caycedo a exerc en Espagne o il a dvelopp cette technique en s'inspirant de diverses disciplines occidentales et orientales. Son objectif tait de crer une mthode permettant d'amliorer la qualit de vie et le bien-tre mental et physique des individus.</p>
            <p>La sophrologie combine des lments de l'hypnose, du yoga, de la mditation, et de la psychologie positive. Le terme "sophrologie" vient du grec ancien, avec "ss" signifiant "harmonie", "phren" signifiant "esprit", et "logos" signifiant "tude". Cela peut se traduire par "l'tude de l'harmonie de l'esprit".</p>
          </div>
        </div>
        <div className="home-content__column home-content__column--middle" data-aos="flip-right" data-aos-offset="200">
          <h2>Pour qui ? Pour quoi ?</h2>
          <div className="home-content__column-content">
            <p>Pour toute personne souhaitant mettre en lumire ses diffrents troubles et les apprhender au mieux : que soit la gestion du stress/des motions, d'une phobie, de retrouver un sommeil harmonieux, de retrouver confiance en soi, de se prparer  un vnement, ou simplement pour se reconnecter  une une respiration paisible ; tout a dans le but de pouvoir se ressourcer quand vous le souhaitez dans votre quotidien. Je suis l pour vous apporter diffrentes clefs vous permettant ensuite de les utiliser pour vous afin de vous retrouver sereinement.</p>
          </div>
        </div>
        <div className="home-content__column" data-aos="flip-right" data-aos-offset="200">
          <h2>Mes outils</h2>
          <div className="home-content__column-content">
            <p>Spcialise dans la gestion du stress et de l'motion, c'est avec une attention encore plus particulire que je vous aiderais  aller au del de vos peurs afin de trouver cette srnit qui est prsente en vous et qui ne demande qu' tre mise en lumire.</p>
            <p>Spcialise galement en entreprise, mon objectif est de contribuer  l'panouissement professionnel et personnel de votre quipe, en proposant des sances adaptes aux besoins de chacun. Ensemble, nous pouvons crer un environnement de travail plus serein et plus productif.</p>
          </div>
        </div>
      </div>

      <div className="home-content__accordions">
        <Accordion>
          <Card>
            <CustomToggle eventKey="0">Petite histoire...</CustomToggle>
            <Accordion.Collapse eventKey="0">
              <Card.Body>
                <p>La sophrologie est une mthode de relaxation et de dveloppement personnel fonde en 1960 par le neuropsychiatre Alfonso Caycedo. Originaire de Colombie, A. Caycedo a exerc en Espagne o il a dvelopp cette technique en s'inspirant de diverses disciplines occidentales et orientales. Son objectif tait de crer une mthode permettant d'amliorer la qualit de vie et le bien-tre mental et physique des individus.</p>
                <p>La sophrologie combine des lments de l'hypnose, du yoga, de la mditation, et de la psychologie positive. Le terme "sophrologie" vient du grec ancien, avec "ss" signifiant "harmonie", "phren" signifiant "esprit", et "logos" signifiant "tude". Cela peut se traduire par "l'tude de l'harmonie de l'esprit".</p>
              </Card.Body>
            </Accordion.Collapse>
          </Card>
          <Card>
            <CustomToggle eventKey="1">Pour qui ? Pour quoi ?</CustomToggle>
            <Accordion.Collapse eventKey="1">
              <Card.Body>
                <p>Pour toute personne souhaitant mettre en lumire ses diffrents troubles et les apprhender au mieux : que soit la gestion du stress/des motions, d'une phobie, de retrouver un sommeil harmonieux, de retrouver confiance en soi, de se prparer  un vnement, ou simplement pour se reconnecter  une une respiration paisible ; tout a dans le but de pouvoir se ressourcer quand vous le souhaitez dans votre quotidien. Je suis l pour vous apporter diffrentes clefs vous permettant ensuite de les utiliser pour vous afin de vous retrouver sereinement.</p>
              </Card.Body>
            </Accordion.Collapse>
          </Card>
          <Card>
            <CustomToggle eventKey="2">Mes outils</CustomToggle>
            <Accordion.Collapse eventKey="2">
              <Card.Body>
                <p>Spcialise dans la gestion du stress et de l'motion, c'est avec une attention encore plus particulire que je vous aiderais  aller au del de vos peurs afin de trouver cette srnit qui est prsente en vous et qui ne demande qu' tre mise en lumire.</p>
                <p>Spcialise galement en entreprise, mon objectif est de contribuer  l'panouissement professionnel et personnel de votre quipe, en proposant des sances adaptes aux besoins de chacun. Ensemble, nous pouvons crer un environnement de travail plus serein et plus productif.</p>
              </Card.Body>
            </Accordion.Collapse>
          </Card>
        </Accordion>
      </div>
    </div>
  );
}

export default HomeContent;
