import React, { useEffect } from 'react';
import Banner from './Banner';
import TitleContent from './TitleContent';
import AOS from 'aos';
import 'aos/dist/aos.css';
import aboutImage from '../assets/images/about.webp';
import lucieImage from '../assets/images/lucie.webp';

function About() {
  useEffect(() => {
    const handleResize = () => {
      const elements = document.querySelectorAll('[data-aos]');
      elements.forEach((element) => {
        if (window.innerWidth <= 768) {
          element.setAttribute('data-aos', 'flip-right');
        } else {
          element.setAttribute('data-aos', 'flip-up');
        }
      });
      AOS.refresh();
    };

    window.addEventListener('resize', handleResize);
    handleResize(); // Call it initially to set the correct animation

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  useEffect(() => {
    AOS.init({ duration: 1000 });
  }, []);

  return (
    <div className="about">
      <Banner src={aboutImage} />
      <TitleContent 
        title="Qui suis-je ?" 
        subtitle=""
      />
      <div className="about__card" data-aos="flip-up">
        <img src={lucieImage} alt="Lucie Plumeria" className="about__card-image" />
        <div className="about__card-text">
          <p>
             cur de comprendre la complexit de l'tre humain et de favoriser son bien-tre, j'ai emprunt un chemin de dcouverte et de transformation personnelle. Se connatre pleinement et atteindre une paix intrieure sont les raisons profondes qui m'ont pousse  m'engager dans un vritable voyage intrieur, enrichi par diverses pratiques et disciplines. Convaincue que le travail sur soi est indispensable pour aider les autres, je me suis dote de cette devise fondamentale : c'est en explorant et en harmonisant notre propre tre que nous pouvons vritablement soutenir ceux qui nous entourent.
          </p>
          <p>
            Aujourd'hui, je suis comble de possder un ventail d'outils diversifis qui me permettent d'offrir une aide prcieuse et quotidienne  chacun. Mon objectif ultime est de semer la srnit et la paix, afin que vous puissiez vous panouir pleinement et retrouver une confiance inbranlable en vous-mme. Dote d'une curiosit insatiable et d'une empathie naturelle, je m'engage  tre une oreille attentive et bienveillante, prte  illuminer les zones d'ombre de votre tre et  rvler la lumire intrieure qui ne demande qu' briller.
          </p>
          <p>
            Il me tient  cur de vous guider vers une connexion authentique avec votre essence vritable, vous permettant ainsi de rayonner dans la vie que vous mritez pleinement. Je serais honore de vous accompagner sur ce chemin de dcouverte de soi, vous aidant  accder  cette paix intrieure et  cette harmonie qui transformeront votre quotidien. Ensemble, nous ouvrirons la porte  une vie riche de sens et de plnitude, o votre vritable potentiel pourra s'panouir et s'exprimer librement.
          </p>
        </div>
      </div>
    </div>
  );
}

export default About;
